@use "sass:map";
@use "../globals";

// stylelint-disable declaration-no-important

// Shadows

@if globals.$feature-shadows {
  .shadow-sm {
    box-shadow: map.get(globals.$shadows, 1) !important;
  }
  .shadow {
    box-shadow: map.get(globals.$shadows, 2) !important;
  }
  .shadow-lg {
    box-shadow: map.get(globals.$shadows, 3) !important;
  }
  .shadow-none {
    box-shadow: map.get(globals.$shadows, 0) !important;
  }
}
